.login-register {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;

  .login-panel {
    width: 100%;
    height: 100vh;
    padding: 36px;
    display: flex;
    flex-direction: column;
    gap: 32px;
    justify-content: flex-start;

    .flex-item1 {
      .logo {
        width: 48px;
        margin-bottom: 16px;

        img {
          object-fit: contain;
          width: 100%;
        }
      }
    }

    .flex-item2 {
      // flex-grow: 1;
      display: flex;
      flex-direction: column;
      // align-items: center;
      justify-content: center;

      .login-form {
        .form-field-full-width {
          width: 100%;
        }
      }
    }

    .flex-item3 {
      width: 100%;
    }

    .buttons {
      align-self: flex-end;
      display: flex;
      flex-wrap: wrap;
      gap: 24px;
      justify-content: flex-end;
    }

    .create-submit-flex {
      display: flex;
      gap: 24px;
      width: 100%;

      & button {
        flex: 1;
      }
    }

    .guest-login-button {
      width: 100%;
    }
  }
}

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

.spinner:before {
  content: '';
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin-top: -10px;
  margin-left: -10px;
  border-radius: 50%;
  border: 2px solid #ffffff;
  border-top-color: #000000;
  animation: spinner 0.8s linear infinite;
}

@media (min-width: 600px) {
  .login-register {
    .login-panel {
      height: auto;
      justify-content: space-between;
      border-radius: 28px;
      width: 480px;
    }
  }
}

@media (min-width: 960px) {
  .login-register {
    .login-panel {
      min-width: 840px;
      flex-direction: row;
      height: 402px;
      flex-wrap: wrap;

      .flex-item {
        flex: 0 0 45%;
      }

      .flex-item2 {
        align-self: flex-end;
      }

      .flex-item3 {
        // height: 40px;
        margin-left: auto;
      }
    }
  }
}

@media (min-width: 1240px) {
  .login-register {
    .login-panel {
      width: 70vw;
    }
  }
}

@media (min-width: 1600px) {
  .login-register {
    .login-panel {
      width: 1040px;
    }
  }
}
